import Vue from 'vue'
import Router from 'vue-router'
import store from '../src/components/store/store'
import * as types from '../src/components/store/types'
import index from '../src/components/index.vue'//绑定页面
import Reply from '../src/components/reply.vue'//问题回复页面
import NewsList from "../src/components/newsList.vue"
import activityList from "../src/components/activityList.vue"
import article from "../src/components/article.vue"
import news from "../src/components/news.vue"
Vue.use(Router)

const routes = [
  { path: '/index/:code', name: 'index', component: index},
  {path: '/Reply/:id', name: 'Reply', component: Reply},
  {path: '/NewsList', name: 'NewsList', component: NewsList},
  {path: '/activityList', name: 'activityList', component: activityList},
  {path: '/article/:id', name: 'article', component: article},
  { path: '/news/:id', name: 'news', component: news },
  { path: '*', redirect: '/NewsList' },
]

// 页面刷新时，重新赋值token
if (window.localStorage.getItem('token')) {
  store.commit(types.LOGIN, window.localStorage.getItem('token'))
}
if (window.localStorage.getItem('userinfo')) {
  store.commit(types.USERINFO, window.localStorage.getItem('userinfo'))
}
const router = new Router({
  linkActiveClass: 'active',
  mode: 'hash',
  base: __dirname,
  routes: routes
});
router.beforeEach((to, from, next) => {
  if (to.matched.some(r => r.meta.requireAuth)) {
    if (store.state.token) {
      window.scrollTo(0, 0);
      next();
    }
    else {
      next({
        path: '/index',
        query: { redirect: to.fullPath }
      })
    }
  }
  else {
    window.scrollTo(0, 0);
    next();
  }
})
export default router;



