<template>
  <div style="margin-top:300px;" class="activityList" v-infinite-scroll="loadMore" infinite-scroll-immediate-check="false" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
    <router-link :to="{ name: 'article', params: { id: item.id}}" class="item" v-for="(item, index) in list" :key="index">
      <img :src="item.icon" alt="" class="item_img">
      <div class="item_word">
        <h1 class="text-ellipsis-muti text-ellipsis-1">{{item['name']}}</h1>
        <p class="text-ellipsis-muti text-ellipsis-1">时间：{{item['location']}}</p>
        <p>地点：{{item['startAt'] | formatDate}}</p>
      </div>
    </router-link>
  </div>
</template>

<script>
  import api from "../api/api.js";
  import {
    formatDate
  } from '../../static/js/date.js'
  export default {
    data() {
      return {
        active: 0,
        activtyData: "",
        totalPages: "",
        list: [],
        last: "",
        page: 1,
        loading: false
      }
    },
    created() {
      this.initActivity();
    },
    filters: {
      formatDate(time) {
        let date = new Date(time)
        return formatDate(date, 'yyyy-MM-dd')
      }
    },
    methods: {
      initActivity() {
        var url = "/activity/1/" + "6" + "/" + this.page;
        api.Get(url).then(res => {
          this.activtyData = res["data"];
          this.totalPages = res["totalPages"];
          if(this.activtyData.length > 0) {
            for (let i = 0; i < this.activtyData.length; i++) {
              this.list.push(this.activtyData[i]);
            }
          } else if(this.activtyData.length == 0 && this.page > 1) {
            console.log('数据加载完了');
          }
        });
        this.loading = false;
      },
      loadMore() {
        this.loading = true;
        if (this.page <= this.totalPages) {
          this.initActivity(this.page);
        } else {
          return false;
        }
        this.loading = false;
        this.page++;
      }
    }
  }
</script>




