<template>
  <div v-html="content">
  </div>
</template>

<script>
  import api from "../api/api.js";

  export default {
    data() {
      return {
        article: "",
        content: ""
      };
    },
    created() {
      let id = this.$route.params.id;
      this.initArticle(id);
    },
    methods: {
      initArticle(id) {
        api.Get("/activity/" + id).then(res => {
          if (!res['activity']["detail"]["content"]) {
            this.content = "暂无数据";
          } else {
            this.content = res['activity']["detail"]["content"];
          }
        });
      }
    }
  };
</script>

<style>

</style>



