<template>
  <div>
    <div style="min-height:calc(100vh - 2.2rem);">
      <div class="main_login" v-if="!flag">
        <h1>{{question['content']}}</h1>
        <!-- <p>首次创业者必须知道哪些基本常识?首次创业者必须知道哪些基本常识?首次创业者必须知道哪些基本常识?首次创业者必须知道哪些基本常识?首次创业者必须知道哪些基本常识?</p> -->
        <p class="last_p"><span>{{question['name']}}</span>{{question['createAt'] | formatDate}}</p>
      </div>
      <span style="background:linear-gradient(#efefef, #e0e0e0);width:100%;height:0.5rem;display: block"></span>
      <div class="main_login">
        <h2><span>|</span>相关交流</h2>
        <p v-show="reply.length == 0">暂未交流记录</p>
        <ul v-show="reply.length != 0" >
          <li :key="index" v-for="(item, index) in reply">
            <p class="last_p"><span :class="{ blue: item['mentor']}">{{item['name']}}</span>{{item['createAt'] | formatDate}}</p>
            <p class="border-b">{{item['content']}}</p>
          </li>
        </ul>
      </div>
    </div>
    <div class="footer" style="background-color:#fff;">
      <input type="text" placeholder="我来回复" v-model="form.content" @blur="vailEmpty(form.content)">
      <button  @click="submitForm">回复</button>
    </div>
  </div>
</template>

<script>
import { Toast } from "mint-ui";
import api from "../api/api.js";
import { formatDate } from "../../static/js/date.js";
export default {
  data() {
    return {
      list: "",
      question: "", //主题
      reply: "", //对话
      flag: false,
      form: {
        content: "",
        flag: false
      }
    };
  },
  created() {
    let id = this.$route.params.id;
    this.initDetail(id);
  },
  filters: {
    formatDate(time) {
      let date = new Date(time);
      return formatDate(date, "yyyy-MM-dd hh:mm");
    }
  },
  methods: {
    initDetail: function(id) {
      api.Get("/indie/question/" + id).then(res => {
        if (res["question"] == null) {
          this.flag = true;
        } else {
          this.question = res["question"];
          this.reply = res["reply"];
          this.flag = false;
        }
      });
    },
    vailEmpty: function(val) {
      if (val) {
        if (val === "") {
          this.form.flag = true;
        } else {
          this.form.flag = false;
        }
      } else {
        this.form.flag = true;
      }
    },
    submitForm: function() {
      var params = {
        subjectId: this.$route.params.id,
        content: this.form.content
      };
      api.Post("/indie/reply", params).then(res => {
        if (res["suc"] == true) {
          Toast({
            message: "回复成功",
            duration: 500
          });
          let id = this.$route.params.id;
          this.initDetail(id);
        } else {
          Toast({
            message: "回复失败",
            duration: 500
          });
        }
      });
    }
  }
};
</script>

<style scoped>
.blue {
  color: #0089e3;
}
</style>



