<template>
  <div>

    <div class="login_img">
      <img src="../../static/img/logo.png" alt="">
    </div>
    <!--未绑定-->
    <div class="sh-login-main">
      <div>
        <input class="s1" type="text" placeholder="输入手机号" v-model="loginForm.phone" @blur="vailPhone(loginForm.phone)" />
        <p :class="loginForm.phoneFlag ? 'true' : 'false'">* 请输入正确手机号!</p>
      </div>
      <div>
        <input class="s2" type="password" placeholder="输入账号密码" v-model="loginForm.pass" @blur="vailPass(loginForm.pass)" />
        <p :class="loginForm.passFlag ? 'true' : 'false'">* 请输入正确密码!</p>
      </div>
      <button class="button" @click="submitForm">绑定微信</button>
    </div>
  </div>
  </div>
</template>

<script>
  import * as types from './store/types'
  import api from "../api/api.js";
  export default {
    data() {
      return {
        token: '',
        loginForm: {
          phone: "",
          pass: "",
          phoneFlag: false,
          passFlag: false
        },
      }
    },
    mounted() {
      this.$store.commit(types.TITLE, 'Login');
      this.$store.commit(types.TITLE, 'userinfo');
    },
    methods: {
      vailPhone: function(val) {
        let re = /^1[34578]\d{9}$/;
        if (val) {
          if (val === "" || !re.test(val) || val.length < 11) {
            this.loginForm.phoneFlag = true;
          } else {
            this.loginForm.phoneFlag = false;
          }
        } else {
          this.loginForm.phoneFlag = true;
        }
      },
      vailPass: function(val) {
        let re = /^[A-Za-z0-9\!\@\#\$\%\^\&\*\(\)\_\+\`\~\-\=\,\.\;\:\<\>\?\|]{6,16}$/;
        if (val) {
          if (val === "" || !re.test(val) || val.length < 6 || val.length > 16) {
            this.loginForm.passFlag = true;
          } else {
            this.loginForm.passFlag = false;
          }
        } else {
          this.loginForm.passFlag = true;
        }
      },
      submitForm: function() {
        let code = this.$route.params.code;
        var params = {
          "phone": this.loginForm.phone,
          "password": this.loginForm.pass,
          "code": code
        }
        api.Post('/wc/bind', params)
          .then(res => {
            if (res['suc'] == true) {
              this.loginForm.phoneFlag = false;
              this.loginForm.passFlag = false;
              let Authorization = res['msg']; //存取授权值
              let UserInfo = JSON.stringify(res); //存取用户信息
              if (Authorization) {
                this.$store.commit(types.LOGIN, Authorization)
                this.$store.commit(types.USERINFO, UserInfo)
              }
              window.location.href="http://www.sanxiachuanggu.com/wap/#/NewsList"
            } else if (res['suc'] == false) {
              alert(res['msg']);
            }
          });
      }
    }
  }
</script>




