<template>
  <div id="newsList">
    <div class="new_img_banner" id="new_img_banner">
      <img src="../../static/img/12.jpg" alt="">
    </div>
    <div class="news_list" id="news_list">
      <mt-navbar v-model="active" class="new">
        <mt-tab-item :id="item.id" :key="index" v-for="(item, index) in category">{{item['name'] | beforeFour}}</mt-tab-item>
      </mt-navbar>
      <mt-tab-container v-model="active">
        <mt-tab-container-item :key="index" v-for="(item, index) in category" :id="item.id">
          <div v-infinite-scroll="loadMore" infinite-scroll-immediate-check="true" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
            <router-link :to="{ name: 'news', params: { id: item.id}}" class="item" v-for="(item, index) in list" :key="index">
              <img :src="item.icon" alt="" class="item_img">
              <div class="item_word">
                <h1 class="text-ellipsis-muti text-ellipsis-2">{{item['title']}}</h1>
              </div>
            </router-link>
          </div>
        </mt-tab-container-item>
      </mt-tab-container>
    </div>
  </div>
</template>

<script>
  import api from "../api/api.js";
  export default {
    data() {
      return {
        active: 0,
        category: "", //分类
        newsListData: "",
        totalPages: "",
        noFlag: false,
        list: [],
        last: "",
        page: 1,
        loading: false
      }
    },
    watch: {
      active: function(val, oldVal) {
        // 这里就可以通过 val 的值变更来确定
        this.active = val;
        window.localStorage.setItem("active", val);
        this.list = []; //list清空
        this.page = 1;
        this.totalPages = 1;
        this.loading = false
        this.loadMore(val)
      }
    },
    created() {
      this.initView()
    },
    methods: {
      initView() {
        let winHeight = 0;
        if (window.innerHeight) {
          winHeight = window.innerHeight;
        } else if ((document.body) && (document.body.clientHeight)) {
          winHeight = document.body.clientHeight;
        }
        // let newsList = document.getElementById('newsList');
        this.newApi();
      },
      newApi() {
        //获取新闻列表基本信息
        var url = "/pub/info/" + 10;
        api.Get(url).then(res => {
          this.category = res["category"];
          for (var i = 0; i < this.category.length; i++) {
            if (i == 0) {
              this.active = this.category[i].id;
            }
          }
        });
      },
      loadMore() {
        this.loading = true;
        if (this.page <= this.totalPages) {
          // this.initNewsList(this.page);
          var active = window.localStorage.getItem("active");
          var url = "/pub/info/" + active + "/" + "6" + "/" + this.page;
          api.Get(url).then(res => {
            this.newsListData = res["data"];
            this.totalPages = res["totalPages"];
            for (let i = 0; i < this.newsListData.length; i++) {
              this.list.push(this.newsListData[i]);
            }
          });
        } else {
          return false;
        }
        this.loading = false;
        this.page++;
      }
    },
    filters: {
      beforeFour: function(value) {
        if (!value) return ''
        value = value.toString()
        return value.substr(0, 4)
      }
    }
  }
</script>




